export default {
  data() {
    return {
      list: [
        {
					id: 0,
					value: 0,
          uname: "农业银行",
					card_num: 12312315,
					// checked: true
        },
        {
					id: 1,
					value: 1,
          uname: "邮政储蓄",
					card_num: 564864656,
					// checked: false
        },
        {
					id: 2,
					value: 2,
          uname: "农业银行",
					card_num: 897988989,
					// checked: false
        }
			],
			current: 0,
			accList: [],
			user_id: ''
    };
  },
  onLoad() {
		this.user_id = uni.getStorageSync("user_id");
		// 获取所有银行卡信息
		this.getAcc()
  },
  methods: {
		// 获取银行卡信息
		getAcc() {
			// uni.request({
			// 	url: 'http://192.168.1.155:8086/WNC/wallet/getAcc',
			// 	data: {
			// 		user_id: 1
			// 	},
			// 	header: {
			// 		'Content-Type': "application/x-www-form-urlencoded; charset=utf-8"
			// 	},
			// 	succeee(res) {
			// 		console.log(res)
			// 	}
			// })
			this.$http.get('/wallet/getAcc', { params: { user_id: this.user_id } }).then(res => {
				this.accList = res.data
			})
		},
    // handle() {
		// 	uni.request({
		// 		url: 'http://192.168.1.155:8086/WNC/wallet/getRecom',
		// 		data: {
		// 			user_id: 1
